.box {
  height: 30px;
  width: 30px;
  border: 1px solid gainsboro;
  background-color: white;
}
.box:hover {
  cursor: pointer;
}
.visited {
  animation: touched 0.6s ease-out forwards;
  animation-iteration-count: 1;
  transform-origin: center;
}
.wall {
  background-color: rgb(84, 84, 84);
  animation: wallBounce 0.4s ease-out forwards;
}

@keyframes wallBounce {
  50% {
    transform: scale(1.2);
    transform-origin: center;
    border-color: rgb(89, 89, 89);
  }
  100% {
    border-color: rgb(89, 89, 89);
  }
}
@keyframes touched {
  0% {
    transform: scale(0.3);
    background-color: yellow;
    border-color: yellow;
    border-radius: 100%;
  }
  50% {
    transform: scale(1.1);
    border-radius: 0px;
    background-color: rgb(0, 119, 255);
    border-color: rgb(0, 119, 255);
  }
  100% {
    background-color: cyan;
    border-color: cyan;
  }
}
